import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, FormBuilder, Validators} from '@angular/forms';
@Component({
  selector: 'app-sign-up',
  templateUrl: './sign-up.component.html',
  styleUrls: ['./sign-up.component.css']
})
export class SignUpComponent implements OnInit {
  signupControl: FormGroup
  constructor(private fb:FormBuilder) {
    
  }
  onSubmit() {
    console.log(this.signupControl.value);
  }
  ngOnInit(): void {
    this.signupControl = this.fb.group({
      email: ['quachtohai@gmail.com',[Validators.required,gmailValidator]],
      password: '',
      subjects: this.fb.group({
        nodejs: false,
        react: true,
        angular: false
      })
    })
  }
}
function gmailValidator(formGroup:FormGroup){
  if (formGroup.value.includes('@gmail.com')){
    return null
  }
  else
  return {gmail:true}
}
