import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-hello',
  templateUrl: './hello.component.html',
  styleUrls: ['./hello.component.css']
})

export class HelloComponent implements OnInit {
  myvar = Math.random()
  constructor() { }

  ngOnInit(): void {
    setInterval(()=>{
      this.myvar = Math.random()
    },1000);
  }

}
